class ImageUploader {
	async upload(file) {
		const data = new FormData();

		data.append('file', file);
		data.append('upload_preset', 'mqx3ic6b');

		const result = await fetch(
			'http://api.cloudinary.com/v1_1/du73cigo9/upload',
			{
				method: 'POST',
				body: data,
			}
		);
		return await result.json();
	}
}

export default ImageUploader;
